<template>

  <section class="categoryList">
    <div class="page-header">
      <h3 class="page-title">
       گزارشات
      </h3>
<!--      <nav aria-label="breadcrumb">-->
<!--        <ol class="breadcrumb">-->
<!--          <li class="breadcrumb-item"><a href="javascript:void(0);">دسته بندی جدید</a></li>-->
<!--        </ol>-->
<!--      </nav>-->
    </div>
    <div class="row">

      <div class="col-lg-12 grid-margin stretch-card">
        <div class="card">
          <div class="card-body text-center pt-0 mt-0">

            <div class="d-flex flex-row justify-content-between align-items-center pt-0 mt-3">
              <h4 class="card-title pt-3 ">کمبود موجودی</h4>
<!--              <router-link class="nav-link" to="/product/productAdd">-->
<!--                <button type="button" class="btn btn-inverse-success btn-fw">محصول جدید</button>-->
<!--              </router-link>-->


            </div>

            <div v-if="loading == true" class="text-secondary mt-3 mb-3" style="font-size: 14px;">
              <span
                  v-if="loading"
                  class="spinner-border spinner-border-sm mb-1 mt-1"
                  role="status"
                  aria-hidden="true"
              ></span>
            </div>
            <div v-else>
              <div v-if="items.length == 0" class="text-secondary mt-3 mb-3" style="font-size: 14px;">موردی یافت نشد</div>
              <table v-else class="table table-hover text-right mt-3">
                <thead>
                <tr>
                  <th scope="col">عنوان</th>
                  <th scope="col">قیمت</th>
                  <th scope="col">موجودی</th>
                  <th scope="col">دسته بندی</th>
                  <th scope="col">عملیات</th>
                </tr>
                </thead>
                <tbody>

                <tr v-for="(item, index) in items"
                    :key="item._id" v-bind:class="{ backgroundDanger: item.stockCount < 5, }">

                  <td>
                    <div class="d-flex flex-row align-items-center" style="width: 35px; height: 35px;">
                      <div v-if="item.image == null"></div>
                      <expandable-image v-else :src=concatenateFullPath(item.image)  class="rounded ml-2 image" alt=""/>
                      {{ item.title }}
                    </div>
                  </td>
                  <td>{{ item.price }}</td>
                  <td>{{ item.stockCount }}</td>
                  <td>{{ item.category || 'مشخص نشده' }}</td>
                  <td>

                    <button v-on:click="removeItem(index)" type="button" class="btn btn-inverse-primary btn-rounded btn-icon mr-2">
                      <i class="mdi mdi-pencil"></i>
                    </button>

                  </td>
                </tr>

                </tbody>
              </table>
            </div>



          </div>
        </div>
      </div>




    </div>
  </section>

</template>

<script>
import moment from 'moment'
import api from '../../const/api'

export default {
  name: 'categoryList',
  data () {
    return {
      loading: false,
      errored: false,
      errorMessage: "",
      success: false,
      items: [],
    }
  },
  mounted() {
    this.getProducts()
  },
  methods: {
    concatenateFullPath: function (image) {
      return  api.root + "/storage/images/" + image
    },
    timestampToDate: function (timestamp) {
      let date = new Date(timestamp * 1000)
      return moment(date).format("YYYY-MM-DD")
    },
    getProducts: function (){

      this.loading = true;
      this.axios
          .get(api.baseURL + '/product/remainingStock', {
            headers: {
              Authorization: "Bearer " + localStorage.token
            }
          })
          .then(response => {

            // eslint-disable-next-line no-console
            console.log(response)
            this.loading = false
            if (response.status == 200 && response.data.status == true) {
              this.items = response.data.data
            } else {
              this.errored = true
              this.errorMessage = response.data.message
            }

          })
          .catch(error => {
            // eslint-disable-next-line no-console
            console.log(error)
            this.loading = false
            this.errored = true
            this.errorMessage = 'دوباره تلاش کنید'
          });

    },
  }
}
</script>

<style>
.image {
  width: 400px;
  max-width: 100%;
}

.backgroundDanger{
  background-color: #FFCDD2;
}
